<template>

  <div>

    <el-col :span="5"><div class="grid-content bg-purple"></div></el-col>
    <el-col :span="13">

      <div class="grid-content bg-purple-light">

        <el-alert
                :closable="false"
                v-show="tagFlag"
                style="margin-bottom: 7px"
                :title="title"
                type="warning">
        </el-alert>

        <el-input value="input" v-model="input" placeholder="请输入主题"></el-input>
        <el-tag
                style="margin-top: 10px;margin-left: 5px"
                v-for="tag in tags"
                :key="tag.name"
                closable
                @close="handleClose(tag)"
                :disable-transitions="false"
                :type="tag.type">
          {{tag.name}}
        </el-tag>
        <el-tag
                @click="reloadTag()"
                style="margin-top: 10px;margin-left: 5px"
                :disable-transitions="false"
                type="success"
        >
          重新选择<i class="el-icon-refresh-right"></i>
        </el-tag>

        <el-upload
                :limit="1"
                :file-list="fileList"
                style="margin-top: 10px"
                :on-change="inFileList"
                :on-remove="handleRemove"
                class="upload-demo"
                drag
                action="https://a.wuruyue.cn/index/Home/sendEssay"
                multiple>
          <img style="height: 100%" v-if="imageUrl" :src="imageUrl" class="avatar">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">请将封面拖到此处，或<em>点击上传</em></div>
          <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
        </el-upload>

        <div id="toolbar-container" class="toolbar"></div>
        <div id="text-container" class="text"></div>

        <el-button @click="subEssay()" style="margin-top: 5px" type="danger">提交</el-button>

      </div>

    </el-col>
    <el-col :span="5"><div class="grid-content bg-purple"></div></el-col>

  </div>

</template>

<script>

    import E from 'wangeditor'
    export default {
        name: 'SendEssay',
        data(){
            return {
                imageUrl:'',
                input: '',
                editor:'',
                tags:[],
                tagsCopy:[],
                title:'',
                fileList:[],
                tagFlag:false
            }
        },
        mounted() {
            let _this = this;
            this.axios.post('index/Home/getTags',{})
                .then(function(response){
                    _this.tags = response.data;
                    _this.tagsCopy = response.data;
                    _this.reloadTag();
                }).catch(err=>{
                    console.log("获取标签失败")
            });
            this.seteditor()
            this.editor.txt.html(this.value)
        },
        methods:{
            inFileList(file, fileList){
                let _this = this;
                if(fileList.length > 0) {
                    var reader = new FileReader();
                    _this.fileList = fileList;
                    reader.readAsDataURL(fileList[0].raw);
                    reader.onload = function (e) {
                        _this.imageUrl = this.result;
                        _this.fileList[0]['url'] = this.result;
                    }
                }
            },
            handleRemove(file,fileList){
              this.fileList = fileList;
            },
            reloadTag(){
                let _this = this;
                _this.tags = [];
                for (let i in _this.tagsCopy){
                    _this.tags.push(_this.tagsCopy[i]);
                }
            },
            subEssay(){
                let _this = this;
                if(_this.input == ''){
                    _this.title = '主题不能为空';
                    _this.tagFlag = true;
                    return false;
                }
                if(_this.tags.length == 0){
                    _this.title = '标签不能为空,请重新选择';
                    _this.tagFlag = true;
                    return false;
                }
                if(_this.fileList.length == 0){
                    _this.title = '封面不能为空';
                    _this.tagFlag = true;
                    return false;
                }
                if (_this.editor.txt.html() == ''){
                    _this.title = '文章不能为空';
                    _this.tagFlag = true;
                    return false;
                }
                _this.title = '';
                _this.tagFlag = false;
                this.axios.post('index/Home/sendEssay',{'title':_this.input,'tags':_this.tags,'fileList':_this.fileList,'editor':_this.editor.txt.html()})
                    .then(function(responser){

                        _this.$message(responser.data.errmsg);

                    }).catch(err=>{
                        console.log("提交失败");
                })
            },
            handleClose(tag) {
                this.tags.splice(this.tags.indexOf(tag), 1);
            },
            seteditor(){
                this.editor = new E('#toolbar-container', '#text-container');
                this.editor.config.menus = [
                    'bold',
                    'fontSize',
                    'italic',
                    'underline',
                    'strikeThrough',
                    'indent',
                    'lineHeight',
                    'foreColor',
                    'backColor',
                    'link',
                    'list',
                    'todo',
                    'justify',
                    'quote',
                    'emoticon',
                    'image',
                    'table',
                    'splitLine',
                    'undo',
                    'redo',
                ];
                this.editor.create();
            }
        }
    }
</script>

