<!--代码管理/后台管理/个人管理-->
<template>
  <el-row>
    <el-col :span="24">
      <div class="grid-content bg-purple font">

        <el-menu
                :default-active="'tab2-'+activeIndex"
                class="el-menu-demo"
                mode="horizontal"
                @select="handleSelect"
                background-color="#fff"
                text-color="#303133"
                active-text-color="#606266"
        >

          <el-menu-item style="top: 0px" v-for="(subItem,index) in MenuTopHtml" :key="'tab-'+index" v-bind:index="activeIndex" class="iconposition" @click="change()" v-show="subItem.parent_menu_id == 0 && subItem.menu_id == 13">
            <span v-bind:class="subItem.menu_icon">{{subItem.menu_name}}</span>
          </el-menu-item>
          <el-menu-item v-for="(subItem,index) in MenuTopHtml" :key="'tab2-'+index" v-bind:index="'tab2-'+subItem.menu_id" v-show="subItem.parent_menu_id == 0 && subItem.menu_id != 13">
            <span v-bind:class="subItem.menu_icon">{{subItem.menu_name}}</span>
          </el-menu-item>

        </el-menu>

      </div>
    </el-col>

    <el-col :span="24">

      <el-col style="width: auto;height: 1000px;overflow:hidden;background:#fff">

        <el-col :span="24" class="scroll" style="height: 690px;overflow-y: auto;border-right: 1px solid #e6e6e6">
          <el-menu
                  style="border-right: 0px"
                  :collapse="isCollapse"
                  :router="true"
                  :default-active="menu_link"
                  class="el-menu-vertical-demo"
                  @open="handleOpen"
                  @close="handleClose"
                  background-color="#fff"
                  text-color="#000"
                  active-text-color="#ffd04b">

            <el-submenu v-bind:index="'info-'+subItem.menu_id" v-for="(subItem,index) in MenuTopHtml" :key="'info-'+index" v-show="subItem.menu_level == 2">

              <template slot="title">
                <i v-bind:class="subItem.menu_icon"></i>
                <span>{{subItem.menu_name}}</span>
              </template>

              <!--<el-menu-item v-bind:router="Item.menu_link" v-for="(Item,inx) in MenuTopHtml" :key="inx" v-show="Item.parent_menu_id == subItem.menu_id" v-bind:index="Item.parent_menu_id+'-'+Item.menu_id">{{Item.menu_name}}</el-menu-item>-->
              <el-menu-item v-for="(Item,inx) in MenuTopHtml" :key="'info1-'+inx" v-show="Item.parent_menu_id == subItem.menu_id" v-bind:index="Item.menu_link">{{Item.menu_name}}</el-menu-item>

            </el-submenu>

          </el-menu>
        </el-col>

      </el-col>

      <router-view></router-view>

    </el-col>

  </el-row>
</template>


<script>

export default {
    name: 'myworld',
    data() {
        return {
            menu_link:'',
            activeIndex:'1',
            isCollapse:true,
            MenuTopHtml:''
        };
    },
    created:function(){
        var _this = this;
        this.axios.post('index/Home/getChildMenu',{'pid':1})
            .then(function(response){
                _this.MenuTopHtml = response.data;
                // _this.onRouteChanged();
            }).catch(err=>{
                console.log("获取菜单失败");
        });
    },
    methods: {
        change(){
            let _this = this;
            if(_this.isCollapse == false){
                _this.isCollapse = true;
            } else {
                _this.isCollapse = false;
            }
        },
        handleSelect(key, keyPath) {//key数字,keyPath数组
            key = key.replace(/tab2-/,'');
            this.activeIndex = key;
            if (key != null) {
                var _this = this;
                this.axios.post('index/Home/getChildMenu', {'pid': key})
                    .then(function (response) {
                        _this.MenuTopHtml = response.data;
                    }).catch(err => {
                    console.log("获取菜单失败");
                });
            }
        },
        handleOpen(key, keyPath) {
        },
        handleClose(key, keyPath) {

        }
    }
}
</script>
