<template>

  <div class="grid-content bg-purple-light scroll" style="overflow-y:scroll;margin-top: 0px;height: 690px">

    <el-col v-for="(files,index) in filesMenu" :key="index" :span="4" style="margin-left: 100px;margin-top: 20px;margin-bottom: 20px">
      <el-card :body-style="{ padding: '0px' }">
        <el-image :src="files.filelisturl" class="image"></el-image>
        <div style="padding: 14px;">
          <span style="font-size: 17px">{{files.file_title}}</span>
          <div class="bottom clearfix">
            <time class="time">{{ files.c_time }}</time>
            <span style="display: block;margin-top: 10px;font-size: 13px">git地址：</span>
            <span style="word-break: break-word;display: block;margin-top: 8px;margin-bottom: 10px;font-size: 12px">{{files.gitfile}}</span>
          </div>
        </div>
      </el-card>
    </el-col>

  </div>

</template>

<script>

    export default {
        name: 'ShowFile',
        data(){
            return {
                filesMenu: {}
            }
        },
        mounted:function(){
            let _this = this;
            this.axios.post("index/Home/getFiles",{})
                .then(function(responser){
                    _this.filesMenu = responser.data;
                }).catch(err=>{
                    console.log("获取失败");
                })
        },
        methods:{

        }
    }
</script>

