<template>

     <div class="grid-content bg-purple-light scroll" style="overflow-y:scroll;margin-top: 0px;height: 690px">

          <el-col :span="8"><div class="grid-content bg-purple"></div></el-col>
          <el-col :span="8">
               <div class="grid-content bg-purple-light">
                    <h2>{{essayMenu[0].title}}</h2>
                    <span style="margin-top: 5px;color: darkslategray;font-size: 12px">{{essayMenu[0].c_time}}</span>

                    <div style="margin-top: 5px">
                         <el-tag v-show="i == 0" style="margin-bottom: 5px;" :key="i" v-for="(tags,i) in essayMenu[0].tags"  :type="tags.type">{{tags.name}}</el-tag>
                         <el-tag v-show="i != 0" style="margin-left: 5px;margin-bottom: 5px;" :key="i" v-for="(tags,i) in essayMenu[0].tags"  :type="tags.type">{{tags.name}}</el-tag>
                    </div>
                    <div  style="margin-top: 5px">
                         <hr/>
                    </div>
                    <p v-html="essayMenu[0].editor" style="margin-top: 10px;font-size: 14px">{{essayMenu[0].editor}}</p>
               </div>
          </el-col>
          <el-col :span="8"><div class="grid-content bg-purple"></div></el-col>

     </div>

</template>

<script>

    export default {
        name: 'EssayDetail',
        data(){
            return {
                essayMenu: {}
            }
        },
        mounted:function(){
            let query = window.location.href.split("?");
            let val = query[1].split("=");
            let _this = this;
            this.axios.post("index/Home/getEssayDetail",{'id':val[1]})
              .then(function(responser){
                  _this.essayMenu = responser.data;
              }).catch(err=>{
              console.log("获取失败");
          })
        },
        methods:{

        }
    }
</script>

