<template>

  <div>

    <el-col :span="5"><div class="grid-content bg-purple"></div></el-col>
    <el-col :span="13">

      <div class="grid-content bg-purple-light" style="margin-top: 150px">

        <el-tag
                style="margin-top: 5px;margin-left: 5px"
                v-for="tag in tags"
                :key="tag.name"
                closable
                @close="handleClose(tag)"
                :disable-transitions="false"
                :type="tag.type">
          {{tag.name}}
        </el-tag>

        <el-input
                class="input-new-tag"
                v-if="inputVisible"
                v-model="inputValue"
                ref="saveTagInput"
                size="small"
                @keyup.enter.native="handleInputConfirm"
                @blur="handleInputConfirm"
        >
        </el-input>
        <el-button style="margin-left: 5px" v-else class="button-new-tag" size="small" @click="showInput">+ 新增标签</el-button>

        <el-tag
                @click="reloadTag()"
                style="margin-top: 10px;margin-left: 5px"
                :disable-transitions="false"
                type="success"
        >
          重新选择<i class="el-icon-refresh-right"></i>
        </el-tag>


        <el-button @click="submitUpload()" style="margin-top: 20px;width: 100px;display: block" round>提交</el-button>

      </div>

    </el-col>
    <el-col :span="5"><div class="grid-content bg-purple"></div></el-col>

  </div>

</template>

<script>

    export default {
        name: 'AddFile',
        data(){
            return {
                input:'',
                gitinput:'',
                tags:[],
                tagsCopy:[],
                inputVisible: false,
                inputValue: ''
            }
        },
        mounted() {
            let _this = this;
            this.axios.post('index/Home/getTags',{})
                .then(function(response){
                    _this.tags = response.data;
                    _this.tagsCopy = response.data;
                    _this.reloadTag();
                }).catch(err=>{
                console.log("获取标签失败")
            });
        },
        methods:{
            showInput() {
                this.inputVisible = true;
                this.$nextTick(_ => {
                    this.$refs.saveTagInput.$refs.input.focus();
                });
            },
            handleInputConfirm() {
                let inputValue = this.inputValue;
                if (inputValue) {
                    let tagarr = {};
                    tagarr['name'] = inputValue;
                    let color = [];
                    color[0] = '';
                    color[1] = 'success';
                    color[2] = 'info';
                    color[3] = 'danger';
                    color[4] = 'warning';
                    let c = Math.floor(Math.random()*4);
                    tagarr['type'] = color[c];
                    tagarr['tag_id'] = 0;
                    tagarr['tag_type'] = c;
                    this.tags.push(tagarr);
                }
                console.log(this.tags);
                this.inputVisible = false;
                this.inputValue = '';
            },
            reloadTag(){
                let _this = this;
                _this.tags = [];
                for (let i in _this.tagsCopy){
                    _this.tags.push(_this.tagsCopy[i]);
                }
            },
            handleClose(tag) {
                this.tags.splice(this.tags.indexOf(tag), 1);
            },
            submitUpload(){
                let _this = this;
                this.axios.post('index/Home/addTag',{"tags":_this.tags})
                    .then(function(responser){
                        _this.$message(responser.data.errmsg);
                    }).catch(err=>{
                    console.log("上传失败")
                });
            }
        }
    }
</script>

