<template>

  <div>

    <el-col :span="5"><div class="grid-content bg-purple"></div></el-col>
    <el-col :span="13">

      <div class="grid-content bg-purple-light">

        <el-input style="margin-top: 100px" value="input" v-model="input" placeholder="请输入主题"></el-input>
        <el-input style="margin-top: 10px" value="gitinput" v-model="gitinput" placeholder="请输入git项目地址"></el-input>
        <el-upload
                :limit="1"
                :file-list="fileList"
                style="margin-top: 10px"
                :on-change="inFileList"
                :on-remove="handleRemoveFileList"
                class="upload-demo"
                drag
                action="https://a.wuruyue.cn/index/Home/uploadFile"
                >
          <img style="height: 100%" v-if="imageUrl" :src="imageUrl" class="avatar">
          <i class="el-icon-upload"></i>
          <div class="el-upload__text">请将封面拖到此处，或<em>点击上传</em></div>
          <div class="el-upload__tip" slot="tip">只能上传jpg/png文件，且不超过500kb</div>
        </el-upload>

        <el-button @click="submitUpload()" style="margin-top: 20px;width: 100px" round>提交</el-button>

      </div>

    </el-col>
    <el-col :span="5"><div class="grid-content bg-purple"></div></el-col>

  </div>

</template>

<script>

    export default {
        name: 'AddFile',
        data(){
            return {
                imageUrl:'',
                fileList:[],
                fileListBase:'',
                input:'',
                gitinput:''
            }
        },
        methods:{
            inFileList(file, fileList){
                var _this = this;
                if(fileList.length > 0) {
                    var reader = new FileReader();
                    this.fileList = fileList;
                    reader.readAsDataURL(fileList[0].raw);
                    reader.onload = function (e) {
                        _this.fileListBase = this.result;
                        _this.imageUrl = this.result;
                    }
                }
            },
            handleRemoveFileList(file,fileList){
                this.fileList = fileList;
                var _this = this;
                if(fileList.length > 0) {
                    var reader = new FileReader();
                    reader.readAsDataURL(fileList[0].raw);
                    reader.onload = function (e) {
                        _this.fileListBase = this.result;
                        _this.imageUrl = this.result;
                    }
                }
            },
            submitUpload(){
                let _this = this;
                this.axios.post('index/Home/uploadFile',{"input":_this.input,"gitinput":_this.gitinput,"fileList":_this.fileListBase})
                    .then(function(responser){
                        _this.$message(responser.data.errmsg);
                    }).catch(err=>{
                    console.log("上传失败")
                });
            }
        }
    }
</script>

