<template>

     <div class="grid-content bg-purple-light scroll" style="overflow-y:scroll;margin-top: 0px;height: 690px">

            <el-col v-for="(essay,index) in essayMenu" :key="index" :span="4" style="margin-left: 100px;margin-top: 20px;margin-bottom: 20px">
              <el-card :body-style="{ padding: '0px' }">
                  <el-image :src="essay.fileList" class="image"></el-image>
                  <div style="padding: 14px;">
                      <span>{{essay.title}}</span>
                      <p style="margin-top: 10px;margin-bottom: 10px;">
                          <el-tag style="margin-left: 5px;margin-bottom: 5px;" :key="i" v-for="(tags,i) in essay.tags"  :type="tags.type">{{tags.name}}</el-tag>
                      </p>
                      <div class="bottom clearfix">
                          <time class="time">{{ essay.c_time }}</time>
                          <el-button @click="entry(essay.article_id)" style="margin-top: 20px" type="text" class="button">进入</el-button>
                      </div>
                  </div>
              </el-card>
            </el-col>

        </div>

</template>

<script>

    export default {
        name: 'ShowEssay',
        data(){
            return {
                essayMenu: {}
            }
        },
        mounted:function(){
            let _this = this;
            this.axios.post("index/Home/getArticle",{})
              .then(function(responser){
                  _this.essayMenu = responser.data;
              }).catch(err=>{
              console.log("获取失败");
          })
        },
        methods:{
            entry:function(article_id){
                window.location.href="myworld.html#/essaydetail?article_id="+article_id;
            }
        }
    }
</script>

