import Vue from 'vue'
import MyWorld from '../components/MyWorld'
import SendEssay from '../components/SendEssay'
import AddFile from '../components/AddFile'
import ShowEssay from '../components/ShowEssay'
import ShowFile from '../components/ShowFile'
import AddTag from '../components/AddTag'
import EssayDetail from '../components/EssayDetail'
import '../plugins/element.js'
import VueRouter from 'vue-router'

import axios from 'axios'
import VueAxios from 'vue-axios'
axios.defaults.baseURL = '/api'
//
Vue.use(VueAxios, axios)
Vue.use(VueRouter)

Vue.config.productionTip = false

const routes =  [

    {
        path: '/sendessay',
        component:SendEssay
    },
    {
        path:'/addfile',
        component:AddFile
    },
    {
        path:'/addtag',
        component:AddTag
    },
    {
        path:'/showessay',
        component:ShowEssay
    },
    {
        path:'/showfile',
        component:ShowFile
    },
    {
        path:'/essaydetail',
        component:EssayDetail
    }
];

 const router = new VueRouter({
     routes
})

axios.interceptors.request.use(
    config => {
        if (config.method === 'post') {
            config.headers['Authorization'] = sessionStorage.getItem("Authorization");
        }
        return config
    },
    error => {
        console.log(error) // for debug
        Promise.reject(error)
    }
);

 let _this=this;
axios.interceptors.response.use(
    response => {
        if(response.data.errno == 30001){
            window.location.href = "/";
        } else if(response.data.errno == 30002){
            _this.$message(response.data.errmsg);
        }else {
            return response;
        }
    },
    error => {
        console.log(error);
        // window.location.href = "/";
    }
)

new Vue({
    render: h => h(MyWorld),
    router
}).$mount('#myworld')
